export default {
	data() {
		return {
			LockArr: [true, true, true, true],
			selected: '',
			videoAd: '',
			onLoadErr: false
		};
	},
	onLoad() {
		try {
			// 从后台获取，修改后返回给后台
		    const value = uni.getStorageSync('unLockgroupNumber');
		    if (value) {
				this.LockArr = value
		    }
		} catch (e) {
		    // error
		}
	},
	methods: {
		unLock(id) {
			uni.showModal({
			    title: '提示',
			    content: '为避免恶意加群，加入开黑群需先观看15s完整视频，是否观看解锁？',
				confirmText: '解锁',
			    success: (res) => {
			        if (res.confirm) {
						this.selected = id
						this.video()
			        } else if (res.cancel) {
			        }
			    }
			})
		},
		getNumber() {
			// get correct group number
			this.LockArr[this.selected] = false
			try {
			    uni.setStorageSync('unLockgroupNumber', this.LockArr);
			} catch (e) {
			    // error
			}
			uni.setClipboardData({
				data: '909975386',
				success: function(res) {
					uni.getClipboardData({
						success: function(res) {
							uni.showToast({
								title: '群号 已复制到剪贴板'
							});
						}
					});
				}
			})
		},
		video() {
			let start, end;
			let videoAd = qq.createRewardedVideoAd({
			adUnitId: 'e2ef4a02df6b0d9a8ebd13beb5781199'
			})
			videoAd.onLoad(function(res){
			})
			videoAd.onError((res)=>{
				uni.showToast({
					title: '加载失败，请去个人中心：反馈意见'
				})
			})
			
			videoAd.onClose((res)=>{
				end = new Date().getTime()
				if ((end-start)/1000 > 15) {
					this.getNumber()
				}
			})
			
			videoAd.load()
			.then(() => {
				videoAd.show().then(() => {
					start = new Date().getTime()
					
				})
				.catch(err => {
				})
			})
			.catch(err => {
			})
		  
		},
		onShareAppMessage(res) {
			uni.showToast({
				title: 'share'
			})
		    return {
		      title: '王者荣耀，免费抽皮肤，和好兄弟开黑...',
		      path: '/pages/tabbat/login/login?parentID=' + this.$store.state.uuid
		    }
		}
	}
};
